<template>
  <div class = "wrapper">
    第 {{ n }} 手
    <div class="row">
      <CellBox @click="onclick(0, $event)" :n="n"/>
      <CellBox @click="onclick(1, $event)" :n="n"/>
      <CellBox @click="onclick(2, $event)" :n="n"/>
    </div>
    <div class="row">
      <CellBox @click="onclick(3, $event)" :n="n"/>
      <CellBox @click="onclick(4, $event)" :n="n"/>
      <CellBox @click="onclick(5, $event)" :n="n"/>
    </div>
    <div class="row">
      <CellBox @click="onclick(6, $event)" :n="n"/>
      <CellBox @click="onclick(7, $event)" :n="n"/>
      <CellBox @click="onclick(8, $event)" :n="n"/>
    </div>
    <!-- <div>{{ map }}</div> -->
    <div>结果：{{ result === null ? `胜负未分` : `胜者是 ${result}` }}</div>
  </div>
</template>

<script>
  //引入cell组件
  import CellBox from './CellBox.vue'
  export default {
    components: {
      CellBox
    },
    data(){
      return {
        n:0,
        //map:3行3列二维数组,值为null
        map: [
          [null, null, null],
          [null, null, null],
          [null, null, null]
        ],
        result: null
      }
    },
    methods:{
      onclick(i,text){
        console.log(`第${i}号被点了，内容是：${text}`)
        this.map[Math.floor(i/3)][i%3] = text
        console.log(this.map)
        this.n += 1
        //map实现井字棋取胜全部逻辑

        // Check rows
        for (let row = 0; row < 3; row++) {
          if (this.map[row][0] === this.map[row][1] && this.map[row][1] === this.map[row][2] && this.map[row][0] !== null) {
            this.result = this.map[row][0];
            return;
          }
        }

        // Check columns
        for (let col = 0; col < 3; col++) {
          if (this.map[0][col] === this.map[1][col] && this.map[1][col] === this.map[2][col] && this.map[0][col] !== null) {
            this.result = this.map[0][col];
            return;
          }
        }

        // Check diagonals
        if (this.map[0][0] === this.map[1][1] && this.map[1][1] === this.map[2][2] && this.map[0][0] !== null) {
          this.result = this.map[0][0];
          return;
        }
        if (this.map[0][2] === this.map[1][1] && this.map[1][1] === this.map[2][0] && this.map[0][2] !== null) {
          this.result = this.map[0][2];
          return;
        }
      }
    }
  }
</script>

<style>
  .row{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .wrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
</style>