<template>
  <div>
    <!-- {{ n }} -->
    <div class = "cell" @click="onclick">
      <template v-if="a" >{{ text }}</template>
      <template v-else></template>
    </div>
  </div>
</template>

<script>
  export default {
    //引入外部变量n
    props: ['n'],
    data(){
      return { a: false, text: ""}
    },
    methods: {
      onclick(){
        if(this.text !== ""){
          return
        }
        this.a = !this.a
        // if(this.n % 2 === 0){
        //   this.text = "O"
        // }
        this.text = this.n%2 === 0 ? "X" : "O"
        this.$emit('click',this.text)
      }
    }
  };
</script>


<style>
  .cell{
    border: 1px solid black;
    height: 100px;
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 80px;
  }

</style>